.edit-profile-screen {
  background-color: var(--color-grey);
  min-height: 90vh;
  padding: 6rem 0;
}

.edit-profile-screen form {
  background-color: var(--color-white);
  max-width: 80rem;
  margin: 0 auto;
  border: 0.2rem solid var(--color-dark-grey);
  padding: 3rem 15rem;
}

.edit-profile-screen form img {
  width: 4rem;
  height: auto;
  cursor: pointer;
}

.edit-profile-screen form button {
  margin-top: 2rem;
  padding: 0.9rem 2rem;
  cursor: pointer;
  background-color: var(--color-dark-blue);
  border: none;
  color: white;
  font-weight: bold;
  border-radius: 0.5rem;
  justify-self: flex-end;
}

.edit-profile-screen form .profile div {
  display: grid;
  grid-template-columns: 1fr 2fr;
  margin-bottom: 1rem;
  gap: 2.5rem;
  justify-items: flex-end;
  align-items: center;
}

.edit-profile-screen form .profile div.submit-button button {
  grid-column: 2 / -1;
  justify-self: flex-start;
}

.edit-profile-screen form .profile .picture-name-and-link {
  position: relative;
}

.edit-profile-screen form .profile .picture-name-and-link div {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  font-size: 2rem;
  font-weight: bold;
  gap: 0.1rem;
}

.edit-profile-screen form .profile .picture-name-and-link div button {
  color: var(--color-dark-blue);
  font-size: 1.5rem;
  background-color: transparent;
  padding: 0;
  margin-top: 0;
}

.edit-profile-screen form .profile input.profilePicturePicker {
  display: none;
}

.edit-profile-screen form label {
  font-size: 2rem;
}

.edit-profile-screen form input,
.edit-profile-screen form textarea {
  width: 100%;
  padding: 0.5rem 1rem;
}

.edit-profile-screen form input:focus {
  border: 0.2rem solid blue;
}

.edit-profile-screen form button:disabled {
  background-color: var(--color-light-blue);
  cursor: auto;
}

div.info span {
  grid-column: 2 / -1;
  color: var(--color-light-grey);
  font-size: 1.3rem;
}

div.profile-saved {
  position: absolute;
  top: 95%;
  width: 100%;
  display: flex;
  align-items: center;
  padding-left: 1rem;
  color: var(--color-white);
  background-color: var(--color-light-grey);
  height: 5.3vh;
  font-size: 1.6rem;
  transform: translateY(100%);
  transition: all 300ms ease-in;
}

div.profile-saved.show {
  transform: translateY(0);
}

div.profile-saved.hide,
.loader.hide {
  display: none;
}

.loader {
  position: absolute;
  top: 0;
  left: 22.5%;
  width: 5rem;
  height: 5rem;
  border: 5px solid var(--color-light-grey);
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
